<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="options"
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      @sort-change="sortFilterFun"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column fixed prop="gb_id" label="/ID" width="150"> </el-table-column>
      <el-table-column prop="title" label="" width="300">
        <template slot-scope="scope">
          <div class="imgnav">
            <el-image
              class="groupBuyImg"
              style="width: 50px; height: 50px"
              :src="scope.row.image"
              fit="fill"
            ></el-image>
            <span class="title-s">{{ scope.row.title }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="goods_detail" label="" width="400">
        <template slot-scope="scope">
          <span v-for="(item, k) in scope.row.goods_detail" :key="k"
            >{{ item.goods_name }}{{ item.goods_num }}{{ item.goods_price / 100 }}{{
              item.goods_unit
            }},</span
          >
        </template>
      </el-table-column>
      <el-table-column prop="total_price" sortable="custom" label="" width="90">
        <template slot-scope="scope">
          <div class="yellow-t">{{ scope.row.total_price / 100 }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="sales" sortable="custom" label="30" width="150">
        <template slot-scope="scope">
          <div class="green-t">{{ scope.row.sales }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="create_time" sortable="custom" label="" width="180">
      </el-table-column>
      <el-table-column prop="expire_time" sortable="custom" label="">
      </el-table-column>
      <el-table-column fixed="right" label="" width="150">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"
            >/</el-button
          >
          <el-button @click="delClick(scope.row)" type="text" size="small"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "goodsItem",
  props: {
    options: Array,
    merchants: Object,
  },
  data() {
    return {
      sortVal: "",
      sortStatus: false,
      order: "asc",
      // statusArr:[{text:'', value:'0'},{text:'', value:'1'},{text:'', value:'2'},{text:'', value:'3'}],
      multipleSelection: "",
      nameArr: [],
    };
  },
  mounted() {},
  methods: {
    //table
    filterTagTable(filters) {
      this.filterMethod(filters);
    },
    //table
    filterMethod(val) {
      var key = [];
      var value = [];
      for (var i in val) {
        key.push(i);
        value.push(val[i]);
      }
      key = key.join(",");
      value = value.join(",");
      //
      this.f_status = value;
      this.$emit("childFun", this.f_status, "", key, 2);
      // if(key=='status' && value){
      //     this.filterGetGoodsList()
      // }else{
      //     this.getGoodsList()
      // }
    },
    // filterGetGoodsList() {
    //     this.pageSign = 2
    //     let data = {page: this.page, status: this.f_status, class_id:this.class_id}
    //     this.$store.dispatch('goods/getGoodsList',data).then((res) => {
    //         this.options = res.data
    //         this.count = res.count
    //     })
    // },
    //table
    sortMethod(val) {
      this.sortVal = val;
      this.sortStatus = !this.sortStatus;
      if (this.sortStatus) {
        this.order = "asc"; //
      } else {
        this.order = "desc"; //
      }
      //
      // this.sortDataFun()
      this.$emit("childFun", this.sortVal, this.order, "", 1);
    },
    //table
    sortFilterFun(column) {
      this.sortMethod(column.prop);
    },
    //
    handleSelectionChange(val) {
      var arr = [];
      for (var i = 0; i < val.length; i++) {
        arr.push(val[i].gb_id);
        this.nameArr.push(val[i].title);
      }
      this.multipleSelection = arr;
      // arr = Array.from(new Set(arr))
      this.nameArr = Array.from(new Set(this.nameArr));
      // this.multipleSelection = arr.join(',')
      this.$emit("childFun", this.multipleSelection, "", this.nameArr, 4);
    },
    //
    handleClick(row) {
      this.$emit("edit", row, false);
      // this.$router.push({
      //   path: "/editGroupBuy",
      //   query: {
      //     gb_id: row.gb_id,
      //     merchant_id: this.merchants.merchant_id,
      //     shop_id: this.merchants.shop_id,
      //   },
      // });
    },
    // 
    delClick(row) {
      this.$emit("childFun", row.gb_id, true, "-" + row.title, 3);
    },
  },
};
</script>

<style scoped></style>
