<!--  -->
<template>
  <div class="uploadFiles">
    <el-upload
      v-show="this.root != ''"
      action="/api/file/uploads"
      list-type="picture-card"
      :limit="limit"
      :file-list="TfileList"
      :on-preview="handlePictureCardPreview"
      :http-request="uploadImg"
      :on-change="handleChange"
      :on-success="handleSuccess"
      :before-upload="beforeAvatarUpload"
      :on-exceed="exceedFun"
      :on-remove="removeFun"
      ref="upload_file"
    >
      <i class="el-icon-plus" />
    </el-upload>
    <h4 v-show="this.root == ''">root...</h4>
    <el-dialog :visible.sync="dialogVisible">
      <img :key="dialogImageUrl" width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="chaochudialogVisible" width="30%">
      <span>{{ tishi }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="chaochudialogVisible = false"> </el-button>
        <el-button type="primary" @click="chaochudialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getToken } from "@/utils/auth"; // get token from cookie

// import { getFileName } from 'ali-oss'
const OSS = require("ali-oss");
export default {
  name: "newUploadFiles",
  props: {
    limit: Number,
    TfileList: Array,
    shop: Boolean,
    root: { type: String, default: "" },
    //dialogVisible:Boolean
  },
  data() {
    return {
      header: {
        headers: {
          "Content-Type": "multipart/form-data; boundary=--------------------------AAAAA",
          AccessToken: getToken(),
        },
      },
      clentSts: {},
      fileLists: [],
      dialogImageUrl: "",
      dialogVisible: false,
      chaochudialogVisible: false,
      tishi: "",
      isAdd: true,
    };
  },
  methods: {
    removeFun(file, fileList) {
      // console.log('',file)
      // console.log('',fileList)
      let arr = [],
        arr2 = [],
        arr3 = [];
      for (var i = 0; i < fileList.length; i++) {
        arr = fileList[i].url.split("/upload");
        arr3[i] = "/upload" + arr[1];
        arr2.push(arr3[i]);
      }
      this.isAdd = false;
      // console.log('', arr2)
      if (this.shop) {
        return;
      } else {
        this.$emit("childrenFun", arr2, this.isAdd);
      }
    },
    exceedFun() {
      if (this.limit == 1) {
        this.chaochudialogVisible = true;
        this.tishi = "1";
      } else if (this.limit == 6) {
        this.chaochudialogVisible = true;
        this.tishi = "6";
      }
    },
    //  
    handleChange(file, fileList) {
      // console.log('change',fileList)
      this.fileLists.push(file.raw);
    },
    /*  */
    uploadImg(option) {
      var upData = new FormData();
      let _this = this;
      this.fileLists.forEach(function (file) {
        // 
        upData.append("file[]", file, file.name);
        upData.append("root", _this.root);
      });
      this.$ajax.defaults.headers.common["token"] = getToken();
      this.$ajax.post("/api/file/uploads", upData, this.header).then((res) => {
        this.isAdd = true;
        let path = res.data.data.url_path; //
        let singlePath = path[path.length - 1]; //
        //this.$refs["upload_file"].clearFiles();
        if (this.limit == 1) {
          this.$emit("childrenFun", singlePath, this.isAdd);
        } else if (this.shop) {
          this.$emit("childrenFun", path, "0");
        } else {
          this.$emit("childrenFun", path, this.isAdd);
        }
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 
    getFileSts() {
      this.$store.dispatch("shop/getFileSts").then((res) => {
        this.clentSts = res.data.Credentials;
      });
    },
    // 
    async handleUploadSTS(option) {
      // action:http://ddhi.oss-cn-hangzhou.aliyuncs.com
      try {
        this.clentSts = await this.$store.dispatch("shop/getFileSts").then((res) => {
          return res.data.Credentials;
          // console.log('filests:', this.fileSts)
        });
        console.log("this.clentSts:", this.clentSts);
        console.log("option:", option);

        // 
        const file = option.file;
        const point = file.name.lastIndexOf(".");
        const suffix = file.name.substr(point);
        // // +
        const storeAs = "product/" + Date.parse(new Date()) + suffix;

        const client = new OSS({
          accessKeyId: this.clentSts.AccessKeyId,
          accessKeySecret: this.clentSts.AccessKeySecret,
          stsToken: this.clentSts.SecurityToken,
          region: "oss-cn-hangzhou", // oss
          bucket: "ddhi",
        });
        // // 
        const ret = await client.multipartUpload(storeAs, file, {
          progress: async function (p) {
            console.log("p:", p);
            const e = {};
            e.percent = p * 100;
            option.onProgress(e);
          },
        });
        console.log(ret);
        if (ret.res.statusCode === 200) {
          // option.onSuccess(ret)
          return ret;
        } else {
          option.onError("");
        }
      } catch (error) {
        console.error(error);
        option.onError("");
        this.$error(error.message);
      }
    },

    // 
    handleSuccess(response) {
      // this.fileLists = fileList
      console.log("response.res.status:", response);
      if (response.res.status === 200) {
        console.log("");
      }
    },

    //
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },
  },
};
</script>

<style lang="scss" scoped>
.uploadFiles {
  position: relative;
  .upload {
    width: 300px;
    height: 200px;
    border: 1px solid #eee;
    text-align: center;
    cursor: pointer;
    transition: all 0.2 linear;
    &:hover {
      border: 1px solid #ccc;
      i {
        margin-top: 18px;
      }
    }
    i {
      margin-top: 20px;
      font-size: 100px;
    }
  }
  .avatar {
    width: 100%;
  }
  .el-upload-dragger {
    height: auto !important;
  }
}
</style>
