<template>
  <div class="shopSet">
    <h2 style="margin: 15px 15px 15px 15px; font-size: 24px">
      {{ form.name }}
      <el-button type="text" style="margin-left: 20px" @click="handleOpenOrder"
        ></el-button
      >
    </h2>
    <el-tabs type="border-card" v-model="activeName" @tab-click="tabsFun">
      <el-tab-pane name="shopInfo">
        <span slot="label"><i class="el-icon-date" /> </span>
        <el-form
          ref="formShop"
          :model="form"
          :rules="rules"
          label-width="140px"
          class="basicInfo"
        >
          <el-form-item label="">
            <el-input v-model="form.name" />
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.ename" disabled="" />
          </el-form-item>

          <el-form-item label="" prop="type_id">
            <el-select v-model="form.type_id" placeholder="">
              <el-option
                v-for="item in shopTypeList"
                :key="item.type_id"
                :label="item.type_name"
                :value="item.type_id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" required>
            <div style="display: flex; width: 200px">
              <el-time-picker
                v-model="form.business_start_time"
                value-format="HH:mm"
                format="HH:mm"
                placeholder=""
              >
              </el-time-picker>
              <span style="margin: 0 5px">-</span>
              <el-time-picker
                v-model="form.business_stop_time"
                value-format="HH:mm"
                format="HH:mm"
                placeholder=""
              >
              </el-time-picker>
            </div>
          </el-form-item>

          <el-form-item label="" required>
            <el-input v-model="shopAreaName" @focus="openCitySelect" />
            <citySelect :title="areaTitle" @setArea="setCityAreaName" />
          </el-form-item>

          <el-form-item label="" prop="address">
            <el-input v-model="form.address">
              <el-button @click="selectLocation" slot="append"></el-button>
            </el-input>
            <br />
            <i class="iconfont icon-dingwei" />{{
              form.longitude
            }}
            {{ form.latitude }}
            <div>
              <el-image
                @click="selectLocation"
                :src="
                  'https://restapi.amap.com/v3/staticmap?location=' +
                  form.longitude +
                  ',' +
                  form.latitude +
                  '&zoom=16&size=700*300&markers=mid,,:' +
                  form.longitude +
                  ',' +
                  form.latitude +
                  '&key=ec089479f41e213eb5167410a7be396e'
                "
              >
                <div slot="placeholder" class="image-slot">
                  <span class="dot">...</span>
                </div>
              </el-image>
            </div>
          </el-form-item>

          <el-form-item label="" prop="phone">
            <el-input v-model="form.phone" />
            <span class="form-shan"
              >(0772-2663398;0772-5051498)</span
            >
          </el-form-item>

          <el-form-item label="" prop="basic_price">
            <el-input v-model="form.basic_price" />
          </el-form-item>

          <el-form-item label="" prop="reserve_auto_audit">
            <el-checkbox v-model="form.reserve_auto_audit"
              ></el-checkbox
            >
          </el-form-item>
          <el-form-item label="" prop="shopping_auto_audit">
            <el-checkbox v-model="form.shopping_auto_audit"
              ></el-checkbox
            >
          </el-form-item>

          <el-form-item label="" prop="shopping_pay_online">
            <el-checkbox v-model="form.shopping_pay_online"
              ></el-checkbox
            >
          </el-form-item>

          <!-- <el-form-item label="" prop="reserve_max_duration">
            <el-input
              v-model="form.reserve_max_duration"
              placeholder=""
            />
            <span class="form-shan">()</span>
            <el-tooltip
              class="item"
              effect="dark"
              content="KTV7"
              placement="right"
            >
              <i class="tip-i el-icon-info"></i>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="" prop="reserve_time_interval">
            <el-input
              v-model="form.reserve_time_interval"
              placeholder=""
            />
            <span class="form-shan">()</span>
            <el-tooltip
              class="item"
              effect="dark"
              content="729"
              placement="right"
            >
              <i class="tip-i el-icon-info"></i>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="" prop="reserve_latest_arrive">
            <el-time-picker
              v-model="form.reserve_latest_arrive"
              value-format="HH:mm"
              format="HH:mm"
              placeholder=""
            >
            </el-time-picker>
          </el-form-item>

          <el-form-item label="" prop="reserve_choose_place">
            <el-radio-group v-model="form.reserve_choose_place">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item> -->

          <!-- <el-form-item label="logo" required>
             <el-image 
              style="width: 100px; height: 100px"
              :src="form.logo" 
              :preview-src-list="logos">
            </el-image>
            <uploadFiles @childrenFun="logoUrl" :limit='limitOne' class="uploadTemp" /> 
            <uploadFiles
              class="uploadTemp"
              @childrenFun="logoUrl"
              :limit="limitOne"
              :TfileList="fileListOne"
              root="shop"
            />
            <span class="form-shan"
              >(100px * 100px2M)</span
            >
          </el-form-item> -->
          <el-form-item label="logo" prop="logo">
            <img class="comimg" v-if="imageUrl" :src="imageUrl" />
            <Imgcutter
              @cutDown="cutDown"
              :boxHeight="600"
              :cutWidth="500"
              :cutHeight="500"
              :sizeChange="true"
            ></Imgcutter>
            <span class="form-shan"
              >(500px * 500px2M)</span
            >
          </el-form-item>
          <el-form-item label="" prop="business_status">
            <el-radio-group v-model="form.business_status">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item v-if="form.business_status == 0" label="">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              placeholder=""
              v-model="form.notice"
            >
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="status">
            <el-radio-group v-model="form.status">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="VR" prop="vr">
            <el-radio-group v-model="form.vr">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item v-if="form.vr == 1" label="VR">
            <el-input placeholder="VR" v-model="form.vr_url">
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="reserve">
            <el-radio-group v-model="form.reserve">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item>
            <!-- <el-button @click="resetForm('formShop')"></el-button> -->
            <el-button @click="$router.back()"></el-button>
            <el-button
              type="primary"
              @click="updatedShop('info', 'formShop')"
              :disabled="isNothing"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="" name="shopAlbum">
        <!-- <div class="photosTab">
          <img v-for="(item,index) in dialogImageUrl" :key="index" width="100%" :src="imgUrl+item.album_path" alt="">
        </div>
        <uploadFiles class="uploadTemp"  @childrenFun="photosTabUrl"/> -->
        <div class="videoBox">
          <div class="photo-title"></div>
          <draggable
            class="list-group"
            v-model="videoList"
            :options="{ animation: 20 }"
            @change="draggableChangeVideo"
          >
            <div
              class="img-div"
              v-for="(item, index) in videoList"
              :key="index"
            >
              <video
                preload="none"
                class="v-img"
                :src="item.file_url"
                :poster="item.cover_url"
                controls
                @mouseover="videoMouseover(index)"
                @mouseleave="videoMouseleave(index)"
              ></video>
              <div class="del-icon" v-show="item.status"></div>
              <div
                class="del-icon2"
                v-show="item.status"
                @click="deleteVideoFun(item.video_id)"
                @mouseover="videoMouseover(index)"
                @mouseleave="videoMouseleave(index)"
              >
                <i class="delete-i el-icon-delete"></i>
              </div>
            </div>
            <div>
              <el-alert
                v-show="videoList.length >= video_limit"
                :title="' '"
                type="warning"
                :closable="false"
              >
              </el-alert>
              <uploadVideo
                v-show="videoList.length < video_limit"
                :limit="video_limit"
                ref="uploadVideo"
                class="uploadTemp"
                @childrenFun="GetVideosUrl"
                :shop="isShop"
                root="shopvideo"
              />
            </div>
          </draggable>
        </div>

        <div class="imgBox">
          <div>
            <div class="photo-title"></div>
            <draggable
              class="list-group"
              v-model="fileList"
              :options="{ animation: 20 }"
              @change="draggableChangeFile"
            >
              <div
                class="img-div"
                v-for="(item, index) in fileList"
                :key="index"
              >
                <img
                  class="d-img"
                  :src="item.album_path"
                  alt=""
                  @mouseover="picMouseover(index)"
                  @mouseleave="picMouseleave(index)"
                  @click="showImg(item.album_path, index)"
                />
                <div class="del-icon" v-show="item.status"></div>
                <div
                  class="del-icon2"
                  v-show="item.status"
                  @click="deletePotoFun(item.album_id)"
                  @mouseover="picMouseover(index)"
                  @mouseleave="picMouseleave(index)"
                >
                  <i class="delete-i el-icon-delete"></i>
                </div>
              </div>
            </draggable>
          </div>
          <div>
            <el-alert
              v-show="fileList.length >= photo_limit"
              :title="' '"
              type="warning"
              :closable="false"
            >
            </el-alert>
            <Imgcutter
              v-show="fileList.length < photo_limit"
              @cutDown="cutDown2"
              :boxHeight="600"
              :cutWidth="684"
              :cutHeight="388"
              :sizeChange="true"
            ></Imgcutter>
            <P style="font-size: 14px; color: #8c939d"
              >2M
              {{ photo_limit }} </P
            >
            <!-- <uploadFiles
              class="uploadTemp"
              @childrenFun="photosTabUrl"
              :limit="limit"
              :shop="isShop"
              root="shop"
            /> -->
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" class="summary" name="shopGoods">
        <goods
          :enable="showPanle.shopGoods"
          :merchants="{ merchant_id: merchant_id, shop_id: shop_id }"
        />
      </el-tab-pane>
      <el-tab-pane
        v-if="form.type_id == 2"
        label=""
        class="summary"
        name="shopGroups"
      >
        <group
          ref="shopGroupComponent"
          :enable="showPanle.shopGroups"
          :merchants="{ merchant_id: merchant_id, shop_id: shop_id }"
        />
      </el-tab-pane>
      <el-tab-pane label="" class="summary" name="shopPlace">
        <place-main
          :enable="showPanle.shopPlace"
          :merchants="{ merchant_id: merchant_id, shop_id: shop_id }"
        />
      </el-tab-pane>

      <el-tab-pane label="" name="shopStaff">
        <staff-main
          :enable="showPanle.shopStaff"
          :merchants="{ merchant_id: merchant_id, shop_id: shop_id }"
        />
      </el-tab-pane>
      <el-tab-pane label="" name="shopPrint">
        <print-manage
          :enable="showPanle.shopPrint"
          :merchants="{ merchant_id: merchant_id, shop_id: shop_id }"
        />
      </el-tab-pane>

      <!-- <el-tab-pane label="">
        <div class="photosTab">
          <img width="100%" :src="shopDatas.svg" alt="">
        </div>
        <uploadFiles class="uploadTemp" />
      </el-tab-pane> -->
    </el-tabs>

    <el-dialog
      title=""
      :visible.sync="mapForAdd.dialogVisible"
      custom-class="baidu-map"
      width="80%"
      @opened="showShopMap"
    >
      <amap ref="aMap" @chooseLocation="chooseLocation" :address="form.name" />
    </el-dialog>

    <el-dialog title="" :visible.sync="imgDialogVisible" width="50%">
      <el-carousel
        :autoplay="false"
        indicator-position="none"
        arrow="always"
        height="600px"
        :initial-index="imgIndex"
        v-if="imgDialogVisible"
      >
        <el-carousel-item v-for="(item, index) in fileList" :key="index">
          <div class="imgbox">
            <el-image
              style="height: 600px; width: auto"
              fit="contain"
              :src="item.album_path"
            ></el-image>
          </div>
        </el-carousel-item>
      </el-carousel>
    </el-dialog>

    <!--  -->
    <go-back v-if="showBack"></go-back>
  </div>
</template>
<script>
import { getToken } from "@/utils/auth";
//import baiduMaps from "@/components/BaiduMap";
import uploadFiles from "@/components/uploadFiles/newUploadFiles";
import uploadVideo from "@/components/uploadFiles/videoUpload";
import citySelect from "@/components/citySelect";
//import draggable from "vuedraggable";
import store from "@/store";
import goods from "@/views/goods/components/goods.vue";
import group from "@/views/goods/components/group.vue";
import PlaceMain from "@/views/shopset/components/PlaceMain.vue";
import staffMain from "@/views/staff/components/staffMain.vue";
import printManage from "@/views/shopset/components/printManage.vue";
import amap from "@/components/Amap";

export default {
  name: "ShopSet",
  components: {
    citySelect,
    //baiduMaps,
    uploadFiles,
    uploadVideo,
    //draggable,
    goods,
    group,
    PlaceMain,
    staffMain,
    printManage,
    amap,
  },
  data() {
    return {
      showBack: false,
      video_limit: 3, //
      photo_limit: 9, //
      imgIndex: 0, //
      header: {
        headers: {
          "Content-Type":
            "multipart/form-data; boundary=--------------------------AAAAA",
          AccessToken: getToken(),
        },
      },
      imageUrl: "",
      showPanle: {
        shopAlbum: false,
        shopGoods: false,
        shopGroups: false,
        shopPlace: false,
        shopStaff: false,
        shopPrint: false,
      },
      activeName: "shopInfo",
      shop_id: "",
      merchant_id: "",

      isNothing: false, //
      imgDialogVisible: false,
      imgUrl: "",
      msg: "",
      shopInfoData: {},
      logos: [],
      form: {
        type_id: "", // ID
        type_name: "", //  *
        name: "", //  *
        ename: "", //  *
        phone: "", //  *
        province_id: "", // ID *
        city_id: "", // ID *
        area_id: "", // ID *
        address: "", //  *
        summary: "", // 
        longitude: "", // 
        latitude: "", // 
        logo: "", // logo *
        notice: "", // 
        basic_price: "", // , *
        business_start_time: "", //  *
        business_stop_time: "", //  *
        reserve_max_duration: "", //  *
        reserve_time_interval: "", //  *
        reserve_auto_audit: "", //  *
        shopping_auto_audit: "", //  *
        shopping_pay_online: "", //  *
        reserve_latest_arrive: "", //
        reserve_choose_place: 0,
        business_status: 1, //10
        status_desc: "",
        status: null, //
      },
      rules: {
        business_start_time: [
          { required: true, message: "", trigger: "change" },
        ],
        business_stop_time: [
          { required: true, message: "", trigger: "change" },
        ],
        address: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ required: true, message: "", trigger: "blur" }],
        reserve_max_duration: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        reserve_time_interval: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        basic_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        reserve_latest_arrive: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        reserve_auto_audit: [
          { required: true, message: "", trigger: "change" },
        ],
        shopping_auto_audit: [
          { required: true, message: "", trigger: "change" },
        ],
        shopping_pay_online: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        reserve_choose_place: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        business_status: [
          { required: true, message: "", trigger: "change" },
        ],
        status: [
          { required: true, message: "", trigger: "change" },
        ],
      },
      mapForAdd: {
        dialogVisible: false,
        mapHeight: 450,
        height: "80%",
        width: "85%",
        longitude: "",
        latitude: "",
        title: "",
      },
      openTimeArea: [
        new Date(2016, 9, 10, 0, 11),
        new Date(2016, 9, 10, 23, 59),
      ], // 
      shopTypeList: [],
      shopAlbumSort: {
        fileList: [],
        videoList: [],
      },
      uploadData: {},
      locationData: {},
      areaTitle: "", // 
      shopAreaName: "", // 
      value: "",
      shopDatas: "",
      imgUrl: "",
      dialogImgVisible: true,
      dialogImageUrl: [],
      drawer: false,
      fileList: [],
      fileListOne: [],
      videoList: [],
      limit: 100,
      limitOne: 1,
      isShop: true,
    };
  },
  mounted() {},
  created() {
    // this.imgUrl = window.location.host
    this.showBack = this.$route.query.from ? true : false;
    this.imgUrl = "http://ddhi.oss-cn-hangzhou.aliyuncs.com";
    // console.log(this.imgUrl)
    this.shop_id = this.$route.query.shop_id;
    this.merchant_id = this.$route.query.merchant_id;
    this.activeName = this.$route.query.tab
      ? this.$route.query.tab
      : "shopInfo";
    this.showPanle[this.activeName] = true;
    this.getShopDetail();
    this.getShopType();
  },
  methods: {
    //
    cutDown: function (img) {
      const isLt2M = img.file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message({
          message: "2M",
          type: "warning",
        });
      } else {
        var formData = new FormData();
        formData.append("file[]", img.file);
        formData.append("root", "shop");
        this.$ajax
          .post("/api/file/uploads", formData, this.header)
          .then((res) => {
            if (res.data.code == 0) {
              this.imageUrl = img.dataURL;
              this.form.logo = res.data.data.url_path[0];
            } else {
              this.imageUrl = "";
              this.form.logo = "";
              this.$message({
                message: res.data.msg,
                type: "warning",
              });
            }
          });
      }
    },
    //
    draggableChangeFile(e) {
      console.info(e);
      for (let i = 0; i < this.fileList.length; i++) {
        this.fileList[i]["album_sort"] = i; //this.shopAlbumSort.fileList[i];
      }
      let dataArr = {
        merchant_id: Number(this.merchant_id),
        shop_id: Number(this.shop_id),
        album: this.fileList,
      };
      console.info(dataArr);

      this.api.shop.PhotoSort(dataArr).then((res) => {
        //this.$store.dispatch("shop/updatePic", data).then((res) => {
        this.GetPhotosUrl();
      });
    },
    draggableChangeVideo(e) {
      console.info(e);
      let videoIds = [];
      for (let i = 0; i < this.videoList.length; i++) {
        this.videoList[i]["sort"] = i; //this.shopAlbumSort.videoList[i];
        videoIds.push({
          video_id: this.videoList[i]["video_id"],
          sort: this.videoList[i]["sort"],
        });
      }
      let dataArr = {
        merchant_id: Number(this.merchant_id),
        shop_id: Number(this.shop_id),
        video: videoIds,
      };
      console.info(dataArr);

      this.api.shop.VideoSort(dataArr).then((res) => {
        //this.$store.dispatch("shop/updatePic", data).then((res) => {
        this.GetVideosUrl();
      });
    },
    tabsFun(e) {
      if (e.index == "1") {
        this.GetPhotosUrl();
        this.GetVideosUrl();
      } else if (e.index == "3" && this.showPanle["shopGroups"]) {
        this.$refs.shopGroupComponent.reloadData();
      }
      this.showPanle[e.name] = true;
    },
    /*  */
    getShopDetail() {
      this.fileListOne = [];
      let data = { merchant_id: this.merchant_id, shop_id: this.shop_id };

      //this.$store.dispatch('shop/getShopDetail', data).then((res) => {
      this.api.shop.getShopDetail(data).then(
        (result) => {
          // sessionStorage.setItem('shopDetail', JSON.stringify(res.data))
          let res = result.data;
          this.shopDatas = res.data;
          this.dialogImageUrl = res.data.album;
          var img = res.data.logo;
          this.imageUrl = res.data.logo;
          var imgArr = img.split(",");
          imgArr.forEach((item) => {
            let obj = new Object();
            obj.url = item;
            this.fileListOne.push(obj);
          });
          // console.log('shopDatas:', this.shopDatas)
          this.form = {
            type_id: this.shopDatas.type_id, // ID
            type_name: this.shopDatas.type_name, // 
            name: this.shopDatas.name, // 
            ename: this.shopDatas.ename, // 
            phone: this.shopDatas.phone, // 
            province_id: this.shopDatas.province_id, // ID
            city_id: this.shopDatas.city_id, // ID
            area_id: this.shopDatas.area_id, // ID
            address: this.shopDatas.address, // 
            summary: this.shopDatas.summary, // 
            longitude: this.shopDatas.longitude, // 
            latitude: this.shopDatas.latitude, // 
            logo: this.shopDatas.logo, // logo
            notice: this.shopDatas.notice, // 
            basic_price: this.shopDatas.basic_price / 100, // ,
            reserve_max_duration: this.shopDatas.reserve_max_duration / 3600, // 
            reserve_time_interval: this.shopDatas.reserve_time_interval / 3600, // 
            reserve_auto_audit: this.shopDatas.reserve_auto_audit === 1, // 
            shopping_auto_audit: this.shopDatas.shopping_auto_audit === 1, // 
            shopping_pay_online: this.shopDatas.shopping_pay_online === 1, // 
            business_start_time: this.shopDatas.business_start_time, // 
            business_stop_time: this.shopDatas.business_stop_time, // 
            business_status: this.shopDatas.business_status, // 
            reserve_choose_place: this.shopDatas.reserve_choose_place,
            reserve_latest_arrive: this.shopDatas.reserve_latest_arrive,
            status_desc: this.shopDatas.status_desc,
            status: this.shopDatas.status,
            vr: this.shopDatas.vr,
            vr_url: this.shopDatas.vr_url,
            reserve: this.shopDatas.reserve,
          };
          this.locationData = {
            lng: this.form.longitude,
            lat: this.form.latitude,
            name: this.form.name,
            position: [this.form.longitude, this.form.latitude],
            address: this.form.address,
          };

          this.shopAreaName =
            this.shopDatas.province + this.shopDatas.city + this.shopDatas.area;
          if (this.activeName == "shopAlbum") {
            this.GetPhotosUrl();
            this.GetVideosUrl();
          }
          //
          this.$nextTick(() => {
            this.$refs.uploadVideo.setUploadInfo({
              merchant_id: this.merchant_id,
              shop_id: this.shop_id,
              title: this.form.name,
            });
          });
        },
        (error) => {
          this.isNothing = true;
        }
      );
    },

    // 
    openCitySelect() {
      this._setVuex("showCitySelectBox", true);
    },
    // 
    setCityAreaName(areaName) {
      this.shopAreaName =
        areaName.province.name + areaName.city.name + areaName.area.name;
      this.form.province_id = areaName.province.id;
      this.form.city_id = areaName.city.id;
      this.form.area_id = areaName.area.id;
    },
    // 
    getShopType() {
      this.$store.dispatch("shop/getShopType").then((res) => {
        sessionStorage.setItem("shopType", JSON.stringify(res.data));
        this.shopTypeList = res.data;
      });
    },
    /*  */
    setShopType(item) {
      this.form.type_id = item.type_id;
      // console.log(this.form)
      // this.form.type_name = item.name
    },
    /*  */
    updatedShop(type, formName) {
      const formDatas = this.form;
      const infoDatas = {
        type_id: formDatas.type_id, // ID
        type_name: formDatas.type_name, // 
        name: formDatas.name, // 
        ename: formDatas.ename, // 
        phone: formDatas.phone, // 
        province_id: Number(formDatas.province_id), // ID
        city_id: Number(formDatas.city_id), // ID
        area_id: Number(formDatas.area_id), // ID
        address: formDatas.address, // 
        summary: formDatas.summary, // 
        longitude: formDatas.longitude, // 
        latitude: formDatas.latitude, // 
        logo: this.GetImageUrl(formDatas.logo), // logo
        notice: formDatas.notice, // 
        basic_price: formDatas.basic_price * 100, // ,
        reserve_max_duration: Number(formDatas.reserve_max_duration) * 3600, // 
        reserve_time_interval: Number(formDatas.reserve_time_interval) * 3600, // 
        reserve_auto_audit: formDatas.reserve_auto_audit ? 1 : 0, // 
        shopping_auto_audit: formDatas.shopping_auto_audit ? 1 : 0, // 
        shopping_pay_online: formDatas.shopping_pay_online ? 1 : 0, // 
        business_start_time: formDatas.business_start_time, // 
        business_stop_time: formDatas.business_stop_time, // 
        business_status: formDatas.business_status, // 
        reserve_latest_arrive: formDatas.reserve_latest_arrive,
        reserve_choose_place: formDatas.reserve_choose_place,
        merchant_id: this.merchant_id,
        shop_id: this.merchant_id,
        status_desc: formDatas.status_desc,
        status: formDatas.status,
        vr: formDatas.vr,
        vr_url: formDatas.vr_url,
        reserve: formDatas.reserve,
      };

      const summaryDatas = {
        summary: formDatas.summary,
        merchant_id: this.merchant_id,
        shop_id: this.merchant_id,
      };

      const noticeyDatas = {
        notice: formDatas.notice,
        merchant_id: this.merchant_id,
        shop_id: this.merchant_id,
      };

      const datas =
        type === "info"
          ? infoDatas
          : type === "summary"
          ? summaryDatas
          : noticeyDatas;
      if (type === "info") {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$store.dispatch("shop/updateShop", datas).then((res) => {
              this.$message({
                message: res.msg,
                type: "success",
              });
              this.getShopDetail();
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.$store.dispatch("shop/updateShop", datas).then((res) => {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.getShopDetail();
        });
      }
    },
    // 
    selectLocation(e) {
      this.mapForAdd.dialogVisible = !this.mapForAdd.dialogVisible;
    },
    //
    showShopMap() {
      this.$refs.aMap.setLocation({
        position: [this.form.longitude, this.form.latitude],
        address: this.form.address,
        name: this.form.name,
        lat: this.form.latitude,
        lng: this.form.longitude,
      });
    },
    // 
    closeMaps(str) {
      this.mapForAdd.dialogVisible = str;
    },
    // url
    logoUrl(url) {
      // console.log('url1111',url)
      this.form.logo = url;
    },
    //
    cutDown2: function (img) {
      const isLt2M = img.file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message({
          message: "2M",
          type: "warning",
        });
      } else {
        var formData = new FormData();
        formData.append("file[]", img.file);
        formData.append("root", "shop");
        this.$ajax
          .post("/api/file/uploads", formData, this.header)
          .then((res) => {
            if (res.data.code == 0) {
              this.photosTabUrl(res.data.data.url_path[0], 0);
            } else {
              this.$message({
                message: res.data.msg,
                type: "warning",
              });
            }
          });
      }
    },
    // url
    photosTabUrl(url, types) {
      let type = Number(types);
      let data = [];
      if (type == 0) {
        // console.log('232323')
        data = {
          merchant_id: this.merchant_id,
          shop_id: this.shop_id,
          data: [
            {
              album_path: url,
              album_sort: this.fileList.length,
              album_type: 0, //0
            },
          ],
        };
      } else {
        data = {
          merchant_id: this.merchant_id,
          shop_id: this.shop_id,
          data: [
            {
              album_path: url[url.length - 1].toString(),
              album_sort: this.videoList.length,
              album_type: 1, //1
            },
          ],
        };
      }
      this.api.shop.PhotoInsert(data).then((result) => {
        let res = result.data;
        this.$message({
          message: "" + res.msg,
          type: "success",
        });
        this.GetPhotosUrl();
      });
    },
    //
    GetPhotosUrl(url) {
      this.fileList = [];
      const data = {
        page_size: 10000,
        merchant_id: this.merchant_id,
        shop_id: this.shop_id,
      };
      //
      this.api.shop.shopalbumList(data).then((result) => {
        //this.$store.dispatch("shop/getShopImg", data).then((res) => {
        // console.log('getPhotos:',res)
        this.shopAlbumSort.fileList = [];
        let res = result.data;
        let arr = res.data;
        for (var i = 0; i < arr.length; i++) {
          if (arr[i].album_type == 0) {
            let obj = new Object();
            obj = arr[i];
            obj.status = false;
            this.fileList.push(obj);
            this.shopAlbumSort["fileList"].push(obj.album_sort);
          }
        }

        //this.shopAlbumSort{}
      });
    },
    //
    GetVideosUrl() {
      this.videoList = [];
      const data = {
        page_size: 10000,
        merchant_id: this.merchant_id,
        shop_id: this.shop_id,
      };
      this.api.shop.shopVideoList(data).then((result) => {
        this.shopAlbumSort.videoList = [];
        let res = result.data;
        if (res.data) {
          let arr = res.data;
          for (var i = 0; i < arr.length; i++) {
            this.videoList.push(arr[i]);
            this.shopAlbumSort.videoList.push(arr[i].sort);
          }
        }
      });
    },
    videoMouseover(index) {
      for (var i = 0; i < this.videoList.length; i++) {
        if (i == index) {
          this.videoList[i].status = true;
        }
      }
    },
    videoMouseleave(index) {
      for (var i = 0; i < this.videoList.length; i++) {
        if (i == index) {
          this.videoList[i].status = false;
        }
      }
    },
    picMouseover(index) {
      for (var i = 0; i < this.fileList.length; i++) {
        if (i == index) {
          this.fileList[i].status = true;
        }
      }
    },
    picMouseleave(index) {
      for (var i = 0; i < this.fileList.length; i++) {
        if (i == index) {
          this.fileList[i].status = false;
        }
      }
    },
    deleteVideoFun(id) {
      this.$confirm("", "")
        .then(() => {
          var idArr = [];
          idArr.push(id);
          var data = {
            video_id: idArr,
            shop_id: this.shop_id,
          };
          this.api.shop.shopVideoDelete(data).then((result) => {
            let res = result.data;
            this.$message({
              message: "" + res.msg,
              type: "success",
            });
            this.GetVideosUrl();
          });
        })
        .catch(() => {});
    },
    deletePotoFun(id) {
      this.$confirm("", "")
        .then(() => {
          var idArr = [];
          idArr.push(id);
          var data = {
            id: idArr,
            merchant_id: this.merchant_id,
            shop_id: this.shop_id,
          };
          this.api.shop.PhotoDelete(data).then((result) => {
            let res = result.data;
            this.$message({
              message: "" + res.msg,
              type: "success",
            });
            this.GetPhotosUrl();
          });
        })
        .catch(() => {});
    },
    showImg(imgUrl, i) {
      console.info(i);
      this.imgIndex = i;
      this.imgUrl = imgUrl;
      this.imgDialogVisible = true;
    },
    // 
    chooseLocation(obj) {
      this.locationData = obj;
      this.form.latitude = obj.lat;
      this.form.longitude = obj.lng;
      //this.form.address = obj.province + obj.city + obj.district + obj.addr;
      this.form.address = obj.address;
      this.mapForAdd.dialogVisible = false;
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleOpenOrder() {
      this.$message.warning("...");
    },
  },
};
</script>

